body{
    // direction: rtl;
    margin-top: 80px;
    @media (max-width:991px) {
        margin-top: 0px;
    }

    text-align: right;
   &::-webkit-scrollbar {
        background-color: #eff3f5;
        width: 7px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(21, 128, 241, 0.75);
        border-radius: 100px;
    }
}
.over{
    overflow: hidden;
}
h2,
h3{
    font-weight: unset;
}
.phone-nav{
    width: 100%;
    padding: 20px 0;
    @media (min-width:992px) {
        display: none;
    }
    .nav-con{
        position: relative;
        width: 100%;
    }
    .nav-torger{
        z-index: 200;
        position: fixed;
        left: 13.7%;
        @media (max-width:480px) {
            left: 1.7%;
        }
        top: 15px;
        width: 55px;
        height: 55px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px;
        border-radius: 50%;
        transition: .5s;
        cursor: pointer;
        span{
            width: 28px;
            display: block;
            height: 3px;
            background-color:rgb(15, 36, 96);
            border-radius: 120px;
            transition: .5s;
            &:not(:last-of-type){
                margin-bottom: 5px;
            }
        }
        &.active{
            span{
                background-color:#fff;

                &:nth-child(1){
                    transform: translateY(8px) rotate(135deg);
                }
                &:nth-child(2){
                    transform: rotate(135deg);
                    opacity: 0;

                }
                &:nth-child(3){
                    transform: translateY(-8px) rotate(-135deg);

                }
            }
        }
        &.scrolled{
            background-color: rgba(15, 36, 96,.988);
            transform: scale(1);
            span{
                background-color: #fff;
            }    
        }
    }
    .menu {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        visibility: hidden;
        opacity: 0;
        overflow: hidden;
        backface-visibility: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .375s;
        // background-color: rgba(#eff3f5 ,.5 );
        z-index: 100;
        &::before {
            content: '';
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: -15px;
            background: rgba(15, 36, 96,.988);
            transform-origin: 0 0;
            transform: skew(-14deg) translateX(-120%) ;
            transition: all .275s .1s;
            z-index: 1;
            border-radius: 100px ;
        }        
        ul{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 20;
            margin-bottom: 0;
            padding-inline-start: 0;
            li {
                &:not(:last-of-type){
                    margin-bottom: 22px;
                }
                a{
                    font-size: 20px;
                    color: #fff;
                    font-family: "ITC-W23Md";
                    opacity: 1;
                    text-decoration: none;;
                }
                opacity: 0;
                transform: translateX(50px);
                transition: all .5s;

            }
        }
        &.active{
            opacity: 1;
            visibility: visible;
            &::before {
                transform: skew(0) translateX(0);
                border-radius: 0 ;

                @media (max-width:480px) {
                transform: skew(0) translateX(0);
                    
                }
            }  
            ul li{
                opacity: 1;
                &:nth-of-type(1){
                    transition: all 275ms 175ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(2){
                    transition: all 275ms 225ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(3){
                    transition: all 275ms 275ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(4){
                    transition: all 275ms 325ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(5){
                    transition: all 275ms 375ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(6){
                    transition: all 275ms 425ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(7){
                    transition: all 275ms 475ms;
                    transform: translateX(0);
                }                                      
                &:nth-of-type(8){
                    transition: all 275ms 525ms;
                    transform: translateX(0);
                }                                      
    
            }
        }
        .main-btn {
            font-size: 15px;
              
            i{
                &:nth-of-type(2){
                    left: 16.5%;
                    top: 49%;
                    }
            }
        }
    }
}
.main-nav{
    padding: 40px 0;
    background-color: #fff;
    transition: .3s linear;
    @media (max-width:991px) {
        display: none;    
    }
    &.scrolled{
        padding: 20px 0;
        box-shadow: 0 0 5px rgba(#000,.2)
    }
    .nav-container{
        justify-content: space-between;
        align-items: center;
        ul{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 0;
            padding-inline-start: 0;
            li{
                &:not(:last-child){
                    margin-left: 30px;
                    @media (max-width:1024px) {
                        margin-left: 17px;
                    }
                }
                a{
                    font-size: 16px;
                    color: rgb(15, 36, 96);
                    font-family: "ITC-W23";
                    text-decoration: none;
                    transition: .25s ease-in-out;
                    text-shadow: 0 30px 0px transparent;
                    &:hover{
                        text-shadow: 0 0px 0px #1580f1;
                        color: transparent;
                    }
                }
            }
        }
    }
}
.btn-container{
    text-align: center;
}
.main-btn {
    background-color: #1580f1;
    height: 40px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-family: "ITC-W23Bd";
    color: #fff;
    border-radius: 100px;
    position: relative;
    transition: .5s;
    backface-visibility: hidden;
    i{
        margin-right: 19px;
        font-size: 11px;
        &:nth-of-type(2){
            transition: .5s;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            opacity: 0;
            left: 11.5%;
        
        }
    }
    &:hover{    
        text-decoration: none;
        color: #fff;
        transform: translateX(-6px);
        i{
            opacity: 1;
            animation: color-anim 1s infinite 0.4s;
            &:nth-of-type(2){
                animation: color-anim 1s infinite 0.6s;
            }
        }
    }
}
@keyframes color-anim {
    0% {
        color: white;
    }
    50% {
        color:rgba(#fff,.7);
    }
    100% {
        color: #fff;
    }
}
