.pt{
    img{
        position: absolute;
        opacity: 0;
        right: -20%;
        &:nth-of-type(1){
            top: 10%;
            animation: ptanimte 50s 1s linear infinite alternate ;
        }
        &:nth-of-type(2){
            top: 30%;
            animation: ptanimte 60s 10s  linear infinite alternate ;
            
        }
        &:nth-of-type(3){
            top: 35%;
            animation: ptanimte 60s 20s linear infinite alternate ;
            
        }
        &:nth-of-type(4){
            top: 40%;
            animation: ptanimte 50s 5s linear infinite alternate ;
            
        }
        &:nth-of-type(5){
            top: 50%;
            animation: ptanimte 40s 10s linear infinite alternate ;
            
        }
        &:nth-of-type(6){
            top: 80%;
            animation: ptanimte 60s 15s linear infinite alternate ;
            
        }
        &:nth-of-type(7){
            top: 60%;
            animation: ptanimte 50s  linear infinite alternate ;

        }
        &:nth-of-type(8){
            top: 75%;
            animation: ptanimte 40s 15s linear infinite alternate ;
            
        }

    }
}
@keyframes ptanimte {
    0%{
        opacity: 1;
        right: -20px;
        opacity: 0;
    }
    50%{
        opacity: 1;

        right: 50%;
        transform: rotate(500deg);
    }
    100%{
        right: 100%;
    }
}
