            transform: translateY(-50%);
            transition: .5s;
            fill: rgb(170, 179, 205);
            @media (max-width:991px) {
                right: 31px;
                top: 40%;
            }
        }
    }
    .btn-container{
        margin-top: 18px;
        a{
            margin: auto;
        }
    }
    @media (max-width:991px) {
        
        .btn-container{
            .main-btn {
                width: 100%;
            }
        }
    }
    .plane{
        position: absolute;
        width: max-content;
        top: 0;
        right: 70px;
        @media (max-width:991px) {
            display: none;
        }
    }
    position: relative;
}
.text-box{
    width: max-content;
    position: relative;
    margin-top: 19px;   
    @media (max-width:991px) {
        width: 100%;
    }    
    @media (max-width:991px) {
        margin-top: 0;   
    }
    textarea{
        border-radius:   27px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 4px 10px 0px rgba(15, 36, 96, 0.08);
        width: 787px;
        height: 105px;
        padding: 24px 70px ;
        font-size: 15px;
        font-family: "ITC-W23";
        color: rgb(170, 179, 205);
        border: 2px solid  transparent;
        outline: none;
        transition: .5s;
        @media (max-width:991px) {
            width: 100%;
        }    

    }
    textarea:focus{
        border: 2px solid  rgb(21, 128, 241);
        &::placeholder{
            color: transparent;
        }
    }              
    textarea:focus ~ svg{
        fill: rgb(21, 128, 241);
    }

    svg{
        position: absolute;
        right: 35px;
        @media (max-width:991px) {
            right: 31px;
            
        }
        top: 50%;
        transform: translateY(-119%);
        transition: .5s;
        fill: rgb(170, 179, 205);
        
    }
}
footer{
    padding-bottom: 66px ;
    @media (max-width:991px) {
        padding-bottom: 57px ;

    }
    .footer-con{
        display: flex;
        justify-content: center;
        align-items: center;
        p{
            font-size: 14px;
            color: rgb(101, 115, 153);
            font-family: "ITC-W23";
            margin: 0;
        }
    }
}
